import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import emailjs from 'emailjs-com';
import "aos/dist/aos.css"
import './form.css';

const useStyles = makeStyles((theme) => ({
root: {
    fontFamily: 'Nunito',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    minHeight: '100vh',
    backgroundColor: '#0F0F2D',
    [theme.breakpoints.down("md")]:
    {
        flexDirection: 'column',
    }
},
root1: {
  minWidth: 500,
  minHeight: 600,
  background: 'rgb(0,0,0,0.33)',
  textAlign: 'center',
},
}))
export default function ContactMe() {
  const classes = useStyles();
  const [disable, setDisable] = useState(true)
  function sendEmail(e) {
    e.preventDefault();
    if(disable === false){
    emailjs.sendForm('service_9pyzmwi', 'template_0vtq0bb', e.target, 'user_obBiR8WHsvMenUmg0RzIr')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      e.target.reset()
  }}
  function handleChange(){
    setDisable(false)
  }
  function alertOnClick(){
    if(disable === true)
        alert("Molim vas popunite sve.")
  }

  return (
    <div className={classes.root} id='ContactMe'>
      <div data-aos="fade-up">
        <Card className={classes.root1}>
        <CardContent>
          <h1 className="ContactMe">Kontaktirajte me</h1>
          <form className="container" onSubmit={sendEmail}>
            <div>
              <input type="text" className='form' placeholder="Ime" name='name' />
            </div>
            <div>
              <input type="email" onChange={handleChange} className='form' placeholder="Email" name='email' />
            </div>
            <div>
              <input type="text" className='form' placeholder="Naslov" name='subject' />
            </div>
            <div>
              <textarea className='desc' cols="40" rows="12" placeholder="Poruka" name='message'></textarea>
            </div>
            <div>
              <input onClick={alertOnClick} type='submit' className='btn' value="Pošalji"></input>
            </div>
          </form>
        </CardContent>
      </Card>
      </div>
      </div>
  );
}