import React, { useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import ImageCard from './oMeniKartice'
import projekti from './podaci'
import Aos from "aos";
import "aos/dist/aos.css"
import { Link as Scroll } from 'react-scroll';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { IconButton } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
root: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    minHeight: '100vh',
    background: "#0F0F2D",
    [theme.breakpoints.down("md")]:
    {
        flexDirection: 'column',
    }
},
downicon: {
    display: 'flex',
    color: 'white',
    fontSize: '2rem',
    justifyContent: 'center',
    alignItems: 'center',
    [theme.breakpoints.down("md")]:
    {
        display: 'none',
    }
}
,button: {
    marginTop: '700px',
    textAlign: 'center',
    margin: '-30px',
    [theme.breakpoints.down("md")]:
    {
        margin: '0',
    }
}
}
))
export default function MyWork() {
  const classes = useStyles();
  useEffect(() => {
    Aos.init({duration: 800});
  }, [])


  return (
    <div className={classes.root} id='MyWork'>
        <div data-aos="fade-up-right"><ImageCard place={projekti[0]}/></div>
        <div data-aos="fade-up"><ImageCard place={projekti[1]}/></div>
        <div className={classes.button}>
        <Scroll to="AboutMe" smooth={true} offset={-116}>
                <IconButton >
                    <ExpandMoreIcon className={classes.downicon}/>
                </IconButton>
        </Scroll>
        </div>
        <div data-aos="fade-up"><ImageCard place={projekti[2]}/></div>
        <div data-aos="fade-up-left"><ImageCard place={projekti[3]}/></div>
    </div>
  );
}