import React, { useEffect, useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { AppBar, IconButton, Toolbar } from "@material-ui/core";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';
import { Link as Scroll } from 'react-scroll';
import './links.css'

const useStyles = makeStyles((theme) => ({
root: {
    background: `url(${process.env.PUBLIC_URL + "/assets/bg2.jpg"})`,
    backgroundRepeat: 'no-repeat',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    fontFamily: 'Nunito',
    height: '100vh',
    
}
,appbar: {
    background: '#0F0F2D',
    fontFamily: 'Nunito',
}
,appbarWrapper: {
    width: '80%',
    margin: '0 auto',
}
,Spacing: {
    flexGrow: '3',
}
,Djoxi: {
    cursor: 'pointer',
    flexGrow: '1',
}
,menuicon: {
    color: 'white',
}
,iii: {
    color: 'palegreen',
    flexGrow: '20.7',
    cursor: 'pointer',
}
,logoandtext: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    fontSize: '2rem',
    cursor: 'default',
    color: 'white',
    [theme.breakpoints.down("xs")]:
        {
            fontSize: '1rem',
        } 
    
}
,Port: {
    textAlign: 'center', //klasa u okviru celog kontejnera
}
,logo: {
    height: '40vh',
}
,downicon: {
    display: 'flex',
    color: 'white',
    fontSize: '2rem',
}
,links: {
    flexGrow: '20',
    fontFamily: 'Nunito',
    fontSize: '1.1rem',
    [theme.breakpoints.down("xs")]:
        {
            display: 'none',
        } 
}
,link: {
    color: 'black',
    margin: '10px',
    cursor: 'pointer',
    [theme.breakpoints.down("xs")]:
        {
            display: 'none',
        } 
}
}))
export default function Header() {
  const classes = useStyles();
  const [checked, setChecked] = useState(false)
  useEffect(() => {
      setChecked(true)
  },[])

  return (
    <div className={classes.root} id='Home'>
    <AppBar className={classes.appbar} elevation={0}>
        <Toolbar className={classes.appbarWrapper}>
        <Scroll to='Home' smooth={true}>
        <h1 className={classes.Djoxi}>Đorđ</h1></Scroll><h1 className={classes.iii}>e</h1>
        <div className={classes.links}>
        <Scroll className="links" to='Home' smooth={true} spy={true}  offset={-76} activeClass='active'>
            Početna
        </Scroll>
        <Scroll className="links" to='MyWork' smooth={true} spy={true} offset={-76} activeClass='active'>
             Projekti
        </Scroll>
        <Scroll className="links" to='AboutMe' smooth={true} spy={true} offset={-116} activeClass='active'>
            O meni
        </Scroll>
        <Scroll className="links" to='ContactMe' smooth={true} spy={true} offset={-76} activeClass='active'>
            Kontakt
        </Scroll >
        </div>
        <div className={classes.Spacing}>
        
        </div>
        </Toolbar>
    </AppBar>
        <Collapse in={checked} {... (checked ? { timeout: 1000 } : {})}
         collapsedSize={50}>
        <div className={classes.Port}> 
            <div className={classes.logoandtext}>
            <img className={classes.logo} src='/assets/129png.png' alt='' />
            <h1>Dobrodošli na moj <br/>portfolio website.</h1>
            </div>
            <Scroll to="MyWork" smooth={true}>
                <IconButton>
                    <ExpandMoreIcon className={classes.downicon}/> 
                </IconButton>
            </Scroll>
        </div>
        </Collapse>
    </div>
  );
}