import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import ImageCard from './IskustvoKartice'
import places from './oMeniSlike'
import { Link as Scroll } from 'react-scroll';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { IconButton } from "@material-ui/core";
import "aos/dist/aos.css"

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '100vh',
        fontFamily: 'Nunito',
        marginTop: '40px',
        [theme.breakpoints.down("xs")]:
        {
            flexDirection: 'column',
        }
        ,FrontEnd: {
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
        }
    },
    down1: {
      textAlign: 'center',
      marginTop: '-200px',
      marginBottom: '106px',
    },
    downicon: {
      color: 'white',
      fontSize: '2rem',
    }
    
}))
export default function AboutMe() {
  const classes = useStyles();
  return (
    <>
    <div className={classes.root} id='AboutMe'>
        <div data-aos="fade-up-right"><ImageCard place={places[0]}/> </div>
        <div data-aos="fade-up"><ImageCard place={places[1]}/> </div>
        <div data-aos="fade-up-left"><ImageCard place={places[2]}/> </div>
    </div>
    <div className={classes.down1}>
    <Scroll to="ContactMe" smooth={true}>
                <IconButton>
                    <ExpandMoreIcon className={classes.downicon}/>
                </IconButton>
    </Scroll>
    </div>
    </>
  );
}