import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
  root: {
    minWidth: 400,
    background: 'rgb(1,1,1,0.5)',
    marginTop: '-150px',
  },
  media: {
    height: '120px',
    width: '120px',
    marginLeft: '140px',
    marginBottom: '15px',
  },
  media2: {
    height: '120px',
    width: '80px',
    justifyContent: 'center',
    alignItems: 'center',
    marginLeft: '130px',
    marginBottom: '10px'

  },
  media3: {
    height: '120px',
    width: '120px',
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: '20px',
    marginBottom: '20px',
    marginLeft: '140px',
  }
  ,title: {
      fontFamily: 'Nunito',
      fontWeight: 'bold',
      fontSize: '2rem',
      display: 'flex',
      textAlign: 'center',
      alignItems: 'center',
      justifyContent: 'center',
      color: 'white',
  },
});

export default function MyWorkCard({place}) {
  const classes = useStyles();

  return (
    <Card className={classes.root}>
        <CardContent>
          <Typography gutterBottom variant="h5" component="h1" className={classes.title}>
            {place.title}
          </Typography>
        </CardContent>
        <CardMedia
          className={classes.media}
          image={place.imageUrl}
        />
        <CardMedia
          className={classes.media}
          image={place.imageUrl1}
        />
        <CardMedia
          className={classes.media}
          image={place.imageUrl2}
        />
        <CardMedia
          className={classes.media3}
          image={place.imageUrl3}
        />
    </Card>
  );
}